.page{
  font-family: Manrope, sans-serif;
  color: white;
  line-height: 22.4px;

  scroll-behavior: smooth;

  &__body{
    margin: 0;
  }

  &__menu{
    position: fixed; /* Щоб завжди була на горі */
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;


  &:target{ 
    opacity: 1;
    pointer-events: all;


    }
  }
}

