@mixin on-tablet {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin content-padding-inline(){
  padding-inline: 20px;

  @include on-tablet{
    padding-inline: 72px;
  }

  @include on-desktop{
    max-width: 1200px;
    margin-inline: auto;
    padding-inline: 120px;
  }

}

.conteiner{
  @include  content-padding-inline;
}



@mixin page-grid{
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet{
   --columns: 6;

   column-gap: 24px;
  }

  @include on-desktop{
   --columns: 12;

  }
}


